:root {
  --dark: #160901;
  --grey: #a6a5a4;
  --m-grey: #bfbfbf;
  --light-grey: #d9d9d9;
  --white: #fff;
  --light-brown: #8c7370;
  --brown: #594240;
}

body {
  max-width: auto;
  margin: 0;
  padding: 0;
}

.search-form {
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-bottom: 20px;
  padding: 15px 0;
  display: flex;
}

input {
  height: 25px;
  background-color: rgba(255, 255, 255, .5);
  border: 1px solid #fff;
  border-radius: 25px;
  padding: 10px 15px;
  font-family: Aclonica, sans-serif;
  font-size: 16px;
  box-shadow: 2px 2px 10px #0576ff, 5px 5px 5px #000;
}

input:focus-within {
  background-color: rgba(255, 255, 255, .9);
  border: 1px solid #fff;
  border-radius: 25px;
  outline: rgba(0, 0, 0, 0);
  padding: 10px 15px;
  font-family: Aclonica, sans-serif;
  font-size: 16px;
  box-shadow: 2px 2px 10px #0576ff, 5px 5px 5px #000;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  display: flex;
}

.photo-card {
  width: 325px;
  height: 275px;
  background-color: rgba(255, 255, 255, .8);
  border: 1px solid #000;
  border-radius: 8px;
  display: block;
  box-shadow: 2px 2px 20px rgba(5, 118, 255, .7), 5px 5px 5px rgba(255, 255, 255, .7);
}

.photo-card:hover, .photo-card:focus {
  transition-duration: 1s;
  transform: scale(1.1);
  box-shadow: 2px 2px 20px rgba(5, 118, 255, .7), 5px 5px 5px rgba(0, 0, 0, .7);
}

.pictures {
  width: 325px;
  height: 200px;
  border-bottom: 1px solid #000;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.info {
  justify-content: center;
  align-items: center;
  gap: 15px;
  padding: 0 5px 10px;
  font-family: Aclonica, sans-serif;
  font-size: 13px;
  display: flex;
}

.more-hidden {
  justify-content: center;
  padding: 15px 0;
}

.more-resaults {
  padding: 10px 15px;
}

.rot-360-noscoop:hover {
  filter: hue-rotate(360deg);
  transform: rotate(360deg);
}

.rot-135:hover {
  filter: hue-rotate(135deg);
}

.rot-90:hover {
  filter: hue-rotate(90deg);
}

.b-blue, .b-blue:before {
  background: #0576ff;
  background: -moz-linear-gradient(45deg, c 0%, #24f8ff 100%);
  background: -webkit-gradient( left bottom, right top, color-stop(0%, #0576ff), color-stop(100%, #24f8ff) );
  background: -o-linear-gradient(45deg, #0576ff 0%, #24f8ff 100%);
  background: -ms-linear-gradient( 45deg, #0576ff 0%, #24f8ff 100% );
  filter: progid:DXImageTransform. Microsoft. gradient( startColorstr= "#0576ff", endColorstr= "#24f8ff", GradientType= 1 );
  background: linear-gradient(45deg, #0576ff 0%, #24f8ff 100%);
}

.button2 {
  filter: hue-rotate();
  color: #fff;
  text-shadow: 1px 1px 2px #0576ff, 2px 2px 2px #000;
  border: 1px solid #fff;
  border-radius: 24px;
  margin: .5em;
  padding: 10px;
  font-family: Aclonica, sans-serif;
  font-size: 18px;
  text-decoration: none;
  transition: all .5s;
  display: inline-block;
  box-shadow: 2px 2px 10px #0576ff, 5px 5px 5px #000;
}

h1 {
  text-align: center;
  text-shadow: 2px 2px 20px #0576ff, 5px 5px 5px #000;
  color: #fff;
  margin: 20px;
  font-family: Aclonica, sans-serif;
  font-size: 50px;
}

@media screen and (max-width: 1199px) {
  h1 {
    font-size: 45px;
  }

  .search-form {
    margin-bottom: 10px;
    padding: 0;
  }
}

body, html {
  height: 100%;
  margin: 0;
  padding: 0;
}

.container {
  height: 100%;
  position: relative;
  overflow: visible;
}

.bg {
  width: 100%;
  height: 200%;
  background: url("background.0af4021c.jpg") 0 0 / cover repeat-y;
  animation: scrollBackground 15s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes scrollBackground {
  from {
    background-position: 0 100%;
  }

  to {
    background-position: 0 0;
  }
}

